import {
  Button,
  EmailInput,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Loader } from "../../components";
import { Link, useNavigate, Navigate } from "react-router";
import { RouterPaths } from "../../utils";
import { useAppDispatch, useForm, useAppSelector } from "../../hooks";
import { loginUserThunk } from "../../services/actions/auth/auth-actions";
import { userStorageService } from "../../services/userStorageService";

export const LoginPage = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const user = userStorageService.getUser();
  const { isLoading } = useAppSelector((state) => state.auth);
  const { values, handleChange, handleSubmit } = useForm({
    initialValues: { email: "", password: "" },
    onSubmit: async (data) => {
      try {
        await dispatch(loginUserThunk(data)).unwrap();
        navigate(RouterPaths.MAIN, { replace: true });
      } catch (err) {
        console.error(" ", err);
      }
    },
  });

  if (user) {
    return <Navigate to="/" replace />;
  }

  return (
    <form className="text-align-center" onSubmit={handleSubmit}>
      <h1 className="text text_type_main-medium mb-6"></h1>
      <EmailInput
        data-testid="email-input"
        extraClass="mb-6"
        name="email"
        value={values.email}
        onChange={handleChange}
      />
      <PasswordInput
        data-testid="password-input"
        extraClass="mb-6"
        name="password"
        value={values.password}
        onChange={handleChange}
      />
      <Button
        data-testid="login-btn"
        type="primary"
        extraClass="mb-20"
        htmlType="submit"
        disabled={!values.email || !values.password}
      >
        {isLoading ? <Loader /> : ""}
      </Button>

      <p className="text text_type_main-default text_color_inactive mb-4">
           ?{" "}
        <Link to={RouterPaths.REGISTRATION}></Link>
      </p>
      <p className="text text_type_main-default text_color_inactive">
         ?{" "}
        <Link to={RouterPaths.FORGOT_PASSWORD}> </Link>
      </p>
    </form>
  );
};
