import {
  Button,
  EmailInput,
  Input,
  PasswordInput,
} from "@ya.praktikum/react-developer-burger-ui-components";
import { Loader } from "../../components/loader";
import { RouterPaths } from "../../utils";
import { useAppDispatch, useAppSelector, useForm } from "../../hooks";
import { Link, useNavigate, Navigate } from "react-router";
import { registerUserThunk } from "../../services/actions/auth/auth-actions";
import { userStorageService } from "../../services/userStorageService";

export const RegisterPage = () => {
  const dispatch = useAppDispatch();
  const navigate = useNavigate();
  const user = userStorageService.getUser();
  const { isLoading } = useAppSelector((state) => state.auth);
  const { values, handleChange, handleSubmit } = useForm({
    initialValues: { email: "", name: "", password: "" },
    onSubmit: async (data) => {
      try {
        await dispatch(registerUserThunk(data)).unwrap();
        navigate(RouterPaths.MAIN, { replace: true });
      } catch (err) {
        console.error(" ", err);
      }
    },
  });

  if (user) {
    return <Navigate to="/" replace />;
  }

  return (
    <form className="text-align-center" onSubmit={handleSubmit}>
      <h1 className="text text_type_main-medium mb-6"></h1>
      <Input
        placeholder=""
        extraClass="mb-6"
        name="name"
        value={values.name}
        onChange={handleChange}
      />
      <EmailInput
        extraClass="mb-6"
        name="email"
        value={values.email}
        onChange={handleChange}
      />
      <PasswordInput
        extraClass="mb-6"
        name="password"
        value={values.password}
        onChange={handleChange}
      />
      <Button
        type="primary"
        disabled={!values.email || !values.password || !values.name}
        extraClass="mb-20"
        htmlType="submit"
      >
        {isLoading ? <Loader /> : ""}
      </Button>
      <p className="text text_type_main-default text_color_inactive mb-4">
         ? <Link to={RouterPaths.LOGIN}></Link>
      </p>
    </form>
  );
};
